.NavBar {
  width: 100%;
  height: 75px;
  display: flex;
  display: -webkit-flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
  background-color: var(--navbar);
  color: var(--white);

  &-Contents {
    width: 100%;
    max-width: 1400px;
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
    padding: 0 10px;

    &-CurrentItem {
      border-bottom: 3px solid var(--blue);
    }

    & > * {
      font-size: 1.3rem;
      font-weight: 400;
      cursor: pointer;
      text-transform: uppercase;
      list-style: none;
      padding: 6px 0;

      @media screen and (max-width: 475px) {
        font-size: 20px;
      }
    }
  }
}
