.Stacks {
  color: var(--contrast);

  &-Top {
    display: flex;
    display: -webkit-flex;
    align-items: center;
    padding-bottom: 8px;

    &-Icon {
      font-size: 30px;
    }

    &-Title {
      margin-left: 6px;
      font-size: 25px;
    }
  }

  &-Stack {
    min-height: 400px;
    border: 1px solid var(--lighterGray);

    &-Titles {
      height: 50px;
      display: flex;
      display: -webkit-flex;
      justify-content: space-around;
      align-items: center;
      border-bottom: 1px solid var(--lighterGray);

      & > * {
        cursor: pointer;
        transition: 0.08s ease-in-out;
        font-size: 18px;

        &:hover {
          color: var(--primary);
        }

        @media screen and (max-width: 580px) {
          font-size: 14px;
        }

        @media screen and (max-width: 500px) {
          font-size: 11px;
        }
      }

      &-Current {
        color: var(--primary);
      }
    }
  }
}
