.AboutMe {
  color: var(--contrast);
  max-width: 1400px;

  &-Wrapper {
    display: flex;
    display: -webkit-flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 4rem;

    @media screen and (max-width: 930px) {
      display: flex;
      display: -webkit-flex;
      justify-content: center;
    }

    &-Image {
      height: 400px;
      max-height: 400px;
    }

    &-SubTitle {
      line-height: 2.2rem;
      font-size: 20px;
    }

    &-Introduction {
      margin: 12px 0;
      font-size: 25px;
      color: var(--blue);
    }

    &-ContentsWrapper {
      @media screen and (max-width: 930px) {
        display: flex;
        display: -webkit-flex;
        flex-direction: column;
        -ms-flex-direction: column;
        align-items: center;
        padding-left: 10px;
      }

      &-Contents {
        font-size: 4rem;
        text-align: center;
      }

      &-Training {
        display: flex;
        display: -webkit-flex;
        align-items: center;
        margin-bottom: 6px;

        &-Icon {
          color: var(--orange);
          margin-right: 4px;
          font-size: 1.8rem;
        }

        &-Text {
          font-size: 1.05rem;
          font-weight: bold;
        }
      }

      &-DownloadWrapper {
        display: flex;
        display: -webkit-flex;
        align-items: center;
        margin-bottom: 6px;
      }
    }
  }

  &-EduCertifiWrapper {
    display: flex;
    display: -webkit-flex;
    margin-bottom: 5rem;

    @media screen and (max-width: 700px) {
      flex-wrap: wrap;
    }
  }
}
