  --orange: #e67e22;
  --yellow: #ede139;
  --green: #27ae60;
  --blue: #0984e3;
  --skyblue: #dbedff;
  --navy: #000080;
  --purple: #841dfa;
  --mint: #00d2d3;
  --pink: #e84393;
  --lighterGray: #dfe6e9;
  --white: #ffffff;
  --black: #000000;
  --gray: #b2bec3;
